#page {
	width:980px;
	min-height:100%;
	margin:0px auto;
	min-width:980px;
	position:relative;
}
body._check #page,
body._product #page,
body._welcome #page {
	padding:20px 0px 150px;
}
body._result #page,
body._service #page {
	padding:20px 0px;
}

/*[==Preloader <]*/
#preloader {
	top:0px;
	left:0px;
	width:100%;
	height:100%;
	display:table;
	position:fixed;
	overflow:hidden;
	background:#fff;
	text-align:center;
	z-index:1000000000;
}
#preloader .middle {
	display:table-cell;
	vertical-align:middle;
}
#preloader .middle .label {
	color:#e34842;
	font-size:18px;
	position:relative;
	line-height:normal;
	padding:0px 0px 20px;
	display:inline-block;
	letter-spacing:0.08em;
	// text-transform:uppercase;
    font-family:"CharterC-Bold";
}
#preloader .middle .label:after {
	left:0px;
	bottom:0px;
	content:"";
	width:100%;
	height:20px;
	display:block;
	position:absolute;
	background:url(../img/elements/preloader.gif) center no-repeat;
}
/*[==Preloader >]*/

/*[==Header main <]*/
header.main {
	width:980px;
	height:100px;
	margin:0px auto;
	position:relative;
}
header.main:after {
	clear:both;
	content:"";
	display:block;
}

/*[Logo <]*/
header.main .logo {
	top:0px;
	left:0px;
	width:140px;
	height:100px;
	display:block;
	position:absolute;
	background:url(../img/logo_new.svg) center no-repeat;
}
/*[Logo >]*/

/*[Brand <]*/
header.main .brand._1 {
	top:118px;
	right:13px;
	width:134px;
	height:158px;
	display:block;
	position:absolute;
	background:url(../img/elements/site/single/beer-logo-1-1.png) center no-repeat;
	-webkit-transition:all .2s ease;
	-moz-transition:all .2s ease;
	-ms-transition:all .2s ease;
	-o-transition:all .2s ease;
	transition:all .2s ease;
	z-index: 2;
}
header.main .brand._1:hover,
header.main .brand._1:active {
	background-image:url(../img/elements/site/single/beer-logo-1-2.png);
}

header.main .brand._2 {
	top:1px;
	right:80px;
	width:224px;
	height:78px;
	display:block;
	position:absolute;
	background:url(../img/elements/site/single/beer-logo-2-1.png) center no-repeat;
	-webkit-transition:all .2s ease;
	-moz-transition:all .2s ease;
	-ms-transition:all .2s ease;
	-o-transition:all .2s ease;
	transition:all .2s ease;
	z-index: 2;
}
header.main .brand._2:hover,
header.main .brand._2:active {
	background-image:url(../img/elements/site/single/beer-logo-2-2.png);
}
/*[Brand >]*/

/*[Nav social <]*/
header.main nav.social {
	top:42px;
	right:11px;
	height:35px;
	overflow:hidden;
	position:absolute;
}
header.main nav.social a {
	float:left;
	width:35px;
	height:35px;
	display:block;
	margin-left:18px;
	background-position:center;
	background-repeat:no-repeat;
	-webkit-transition:all .2s ease;
	-moz-transition:all .2s ease;
