}
header.main nav.social a.fb {
	background-image:url(../img/elements/site/ico/social/group-1/1-1.png);
}
header.main nav.social a.fb:hover,
header.main nav.social a.fb:active {
	background-image:url(../img/elements/site/ico/social/group-1/1-2.png);
}
header.main nav.social a.vk {
	background-image:url(../img/elements/site/ico/social/group-1/2-1.png);
}
header.main nav.social a.vk:hover,
header.main nav.social a.vk:active {
	background-image:url(../img/elements/site/ico/social/group-1/2-2.png);
}
header.main nav.social a.ok {
	background-image:url(../img/elements/site/ico/social/group-1/3-1.png);
}
header.main nav.social a.ok:hover,
header.main nav.social a.ok:active {
	background-image:url(../img/elements/site/ico/social/group-1/3-2.png);
}
header.main nav.social a:first-child {
	margin-left:0px;
}
/*[Nav social >]*/

