/*[==Content main <]*/
section.content {
	margin:0px auto;
}

/*[Popup <]*/
.popup {
	top:0px;
	left:0px;
	width:100%;
	height:100%;
	display:none;
	overflow:auto;
	position:fixed;
	z-index:10000000;
	background:url(../img/elements/site/pattern/color/1.png);
}
.popup .align {
	width:100%;
	height:100%;
	display:table;
}
.popup .overlay {
	top:0px;
	left:0px;
	width:100%;
	height:100%;
	display:block;
	cursor:pointer;
	position:absolute;
}
.popup .align .middle {
	padding:15px;
	text-align:center;
	display:table-cell;
	vertical-align:middle;
}

/*[Window <]*/
.popup .window {
	z-index:100;
	margin:0px auto;
	position:relative;
	display:inline-block;
}

/*[Container <]*/
.popup .window .container {
	padding:35px;
	color:#1a2f3f;
	font-size:16px;
	min-width:640px;
	text-align:left;
	line-height:24px;
	background:#e0e0e0;
	border:solid 2px #fff;
    font-family:"CharterC";
	box-shadow:5px 5px 22px 0px rgba(1,1,1,.5);
}
.popup .window .container p {
	margin:15px 0px;
}
.popup .window .container:after {
	clear:both;
	content:"";
	display:block;
}
.popup .window .container .title {
	color:#e34842;
	font-size:28px;
	text-align:center;
	line-height:normal;
	margin-bottom:20px;
    font-family:"CharterC-Bold";
}
.popup .window .container > :last-child {
	margin-bottom:0px;
}
.popup .window .container > :first-child {
	margin-top:0px;
}
/*[Container >]*/

/*[Button close <]*/
.popup .window button.close {
	top:0px;
	right:0px;
	color:#fff;
	width:35px;
	height:35px;
	border:none;
	outline:none;
	font-size:23px;
	overflow:hidden;
	position:absolute;
	text-align:center;
	line-height:normal;
	text-transform:uppercase;
    font-family:"Geometria-Medium";
	background:url(../img/elements/site/button/remove-1-1.png) center no-repeat;
	-webkit-transition:all .1s ease;
	-moz-transition:all .1s ease;
	-ms-transition:all .1s ease;
	-o-transition:all .1s ease;
	transition:all .1s ease;
}
/*[Button close >]*/

/*[Window >]*/

