/*[Player <]*/
.sm2-bar-ui {
	width:460px;
	display:block;
	border-radius:0px;
	min-width:inherit;
	max-width:inherit;
	line-height:normal;
}

/*[Other <]*/
.sm2-bar-ui .bd,
.sm2-bar-ui .sm2-extra-controls {
	background:#fff;
}
.sm2-bar-ui .sm2-button-element {
	border-right:none;
}
.sm2-bar-ui .sm2-menu,
.sm2-bar-ui .sm2-volume,
.sm2-bar-ui .sm2-inline-texture,
.sm2-bar-ui .sm2-inline-gradient,
.sm2-bar-ui .sm2-playlist-drawer {
	display:none;
}
.sm2-bar-ui .sm2-inline-element,
.sm2-bar-ui .sm2-button-element .sm2-button-bd {
	min-width:40px;
	min-height:40px;
}
/*[Other >]*/

/*[Controls <]*/
.sm2-bar-ui .sm2-main-controls {
	width:100%;
	height:40px;
	display:table;
}
.sm2-bar-ui .sm2-main-controls .sm2-inline-element {
	display:table-cell;
	vertical-align:middle;
}
.sm2-bar-ui .sm2-main-controls .sm2-inline-element.sm2-menu,
.sm2-bar-ui .sm2-main-controls .sm2-inline-element.sm2-volume {
	display:none;
}
.sm2-bar-ui .sm2-main-controls .sm2-inline-element.sm2-button-element {
	width:40px;
}

/*[Status <]*/
.sm2-bar-ui .sm2-inline-status {
	padding-left:30px;
	padding-right:0px;
	border-right:none;
}
.sm2-bar-ui .sm2-inline-status .sm2-playlist {
	display:none;
}

/*[Progress <]*/
.sm2-bar-ui .sm2-inline-status .sm2-progress {
	display:block;
	position:relative;
}
.sm2-bar-ui .sm2-inline-status .sm2-progress:before {
	top:0px;
	left:0px;
	content:"";
	width:100%;
	height:100%;
	z-index:100;
	display:block;
	position:absolute;
}
.sm2-bar-ui .sm2-inline-status .sm2-progress .sm2-row {
	display:block;
}
.sm2-bar-ui .sm2-inline-status .sm2-progress .sm2-progress-bd {
	padding:0px;
}
.sm2-bar-ui .sm2-inline-status .sm2-progress .sm2-progress-bar {
	background:#e34842;
}
.sm2-bar-ui .sm2-inline-status .sm2-progress .sm2-progress-ball {
	display:none;
}
.sm2-bar-ui .sm2-inline-status .sm2-progress .sm2-progress-bar,
.sm2-bar-ui .sm2-inline-status .sm2-progress .sm2-progress-track {
	height:10px;
	border-radius:0px;
}
.sm2-bar-ui .sm2-inline-status .sm2-progress .sm2-progress-track {
	position:relative;
	background:#e0e0e0;
}
.sm2-bar-ui .sm2-inline-status .sm2-progress .sm2-inline-time,
.sm2-bar-ui .sm2-inline-status .sm2-progress .sm2-inline-duration {
	right:50px;
	width:auto;
	bottom:100%;
	color:#1a2f3f;
	font-size:14px;
	position:absolute;
	margin-bottom:1px;
	display:inline-block;
    font-family:"CharterC";
}
.sm2-bar-ui .sm2-inline-status .sm2-progress .sm2-inline-duration {
	right:3px;
}
.sm2-bar-ui .sm2-inline-status .sm2-progress .sm2-inline-duration:before {
	content:" / ";
}
/*[Progress >]*/

/*[Status >]*/

/*[Play & pause <]*/
.sm2-bar-ui .play-pause,
.sm2-bar-ui.paused .play-pause {
	opacity:0;
	width:40px;
	height:40px;
	display:block;
	visibility:hidden;
	text-indent:-9999px;
	background:#fff url(../img/elements/site/button/play-1-2.png) center no-repeat;
	-webkit-background-size:contain;
	-moz-background-size:contain;
	-ms-background-size:contain;
	-o-background-size:contain;
	background-size:contain;
}
.sm2-bar-ui.playing .play-pause {
	background:#fff url(../img/elements/site/button/stop-1-2.png) center no-repeat;
}
.sm2-bar-ui .play-pause:hover,
.sm2-bar-ui .play-pause:active,
.sm2-bar-ui.paused .play-pause:hover,
.sm2-bar-ui.paused .play-pause:active {
	background:#fff url(../img/elements/site/button/play-1-4.png) center no-repeat;
}
.sm2-bar-ui.playing .play-pause:hover,
.sm2-bar-ui.playing .play-pause:active {
	background:#fff url(../img/elements/site/button/stop-1-4.png) center no-repeat;
}
/*[Play & pause >]*/

