/*[Button <]*/
button,
.button {
	border:none;
	outline:none;
	cursor:pointer;
	text-align:center;
	position:relative;
	white-space:nowrap;
	display:inline-block;
	text-decoration:none;
	vertical-align:middle;
	line-height:normal !important;
}

/*[1 <]*/
button._1,
.button._1 {
	color:#fff;
	background:none;
	text-align:center;
	text-decoration:none;
	border:solid 2px #fff;
    font-family:"CharterC";
	text-transform:uppercase;
}
button._1:hover,
.button._1:hover,
button._1:active,
.button._1:active {
	background:#e34842;
	border-color:#e34842;
}
/*[1 >]*/

/*[2 <]*/
button._2,
.button._2 {
	color:#fff;
	font-size:16px;
	position:relative;
	text-align:center;
	background:#e34842;
	text-decoration:none;
    font-family:"CharterC";
	text-transform:uppercase;
	border:dotted 2px #f6be93;
	outline:solid 2px #e34842;
}
button._2:before,
.button._2:before {
	left:-4px;
	content:"";
	bottom:-4px;
	display:block;
	position:absolute;
}
button._2:hover:before,
.button._2:hover:before,
button._2:active:before,
.button._2:active:before {
	background:rgba(255,255,255,0);
	background:-moz-linear-gradient(top, rgba(255,255,255,0) 30%, rgba(111,0,20,0.7) 100%);
	background:-webkit-gradient(left top, left bottom, color-stop(30%, rgba(255,255,255,0)), color-stop(100%, rgba(111,0,20,0.7)));
	background:-webkit-linear-gradient(top, rgba(255,255,255,0) 30%, rgba(111,0,20,0.7) 100%);
	background:-o-linear-gradient(top, rgba(255,255,255,0) 30%, rgba(111,0,20,0.7) 100%);
	background:-ms-linear-gradient(top, rgba(255,255,255,0) 30%, rgba(111,0,20,0.7) 100%);
	background:linear-gradient(to bottom, rgba(255,255,255,0) 30%, rgba(111,0,20,0.7) 100%);
}
